<template>
  <div>
    <scroll class="wrapper">
      <div>
        <div class="header">
          <div class="background">
            <img src="~common/image/index/background.png" />
          </div>
          <div class="logo">
            <img src="~common/image/logo.png" />
          </div>
          <div class="catchword">
            <span>财归天下</span>
            <span>价值共生</span>
          </div>
          <div class="contact">
            <a class="mobile" href="tel:15638193839">15638193839</a>
            <span>|</span>
            <span class="link">www.zksszz.com</span>
          </div>
          <div class="address">
            郑州自贸区心怡路南易元国际B座1429室
          </div>
        </div>
        <div class="news-list">
          <ul>
            <li class="item" v-for="item in list" @click="selectItem(item.id)">
              <div class="icon">
                <img :src="item.img" />
              </div>
              <div class="detail">
                <h2 class="title">{{item.title}}</h2>
                <p class="time">{{item.time}}</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import {setLoginPage} from 'common/js/mixin'

  export default {
    mixins: [setLoginPage],
    data () {
      return {
        list: []
      }
    },
    created () {
      this._getList()
    },
    methods: {
      selectItem(id){
        this.$router.push({
          path: `/index/${id}`,
        })
      },
      _getList() {
        for(let i = 0; i < 10; i++){
          let obj = {
            'img':'https://y.gtimg.cn/music/photo_new/T003R720x288M000000yTU5u2XJFWh.jpg?max_age=2592000',
            'title':'中科山水首次合作，与东方维景国际大酒店签约。',
            'time':'2018年1月29日',
            'id': i + 1
          }
          this.list.push(obj)
        }
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .wrapper
    position absolute
    left 0
    right 0
    top 0
    bottom 50px
    .header
      width 100%
      height 250px
      display flex
      flex-direction column
      align-items center
      position relative
      .background
        width 100%
        height 100%
        position absolute
        z-index -1
        img
          display block
          width 100%
          height 100%
      .logo
        width 96px
        height 89px
        margin-top 48px
        img
          display block
          width 100%
          height 100%
      .catchword
        color #fff
        font-size $font-size-large-m
        display flex
        justify-content space-around
        width 185px
        height 24px
        align-items center
        margin-top 14px
      .contact
        font-size $font-size-small-x
        display flex
        justify-content space-around
        align-items center
        color #fff
        width 200px
        height 18px
        margin-top 12px
        margin-bottom 10px
        a
          color #fff
      .address
        font-size $font-size-small-x
        display flex
        justify-content center
        align-items center
        color #fff
        width 100%
        height 18px
    .news-list
      margin 15px 15px 0 15px
      .item
        height 110px
        width 100%
        display flex
        background #fff
        margin-bottom 15px
        border-radius 3px
        box-shadow 0 4px 15px 0 rgba(219, 219, 219, 0.4)
        .icon
          width 110px
          height 110px
          img
            display block
            width 100%
            height 100%
      .detail
        display flex
        flex-direction column
        flex 1
        margin 14px
        justify-content space-between
        .title
          font-size $font-size-medium-m
          color #3d3d3d
          line-height 24px
          font-weight bold
        .time
          font-size $font-size-small
          color #808080

</style>



